* {
  box-sizing: border-box;
}

html {
    background-color: #cad3f5;
}

body {
  padding: 0;
  margin: 0;
}

main {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}

header {
  display: flex;
  gap: 8px;
  height: 40px;
  padding: 8px 1rem;
  background-color: #1e1e2e;
  align-items: center;
  justify-content: center;
}

header h1 {
  font-size: 20px;
  font-weight: 500;
  color: #cad3f5;
  border-right: 2px solid #cad3f5; /*This will be the blinking cursor*/
  animation: cursor 3s infinite step-end;
}


@keyframes cursor{
  0%, 100%{border-color: transparent;}
  50%{border-color: #cad3f5;}
}

/* Style the charts */
/* main .chart {
  width: 50%;
  margin: 20px;
  height: 800px;
} */

main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;
  max-height: max(400px, calc(100vh - 40px));
  min-height: max(400px, calc(100vh - 40px));
  max-width: 100vw;
  min-width: 100vw;
  flex-grow: 1;
}

main > div {
  position: relative;
  height: 100%;
}

@media (orientation: landscape) {
  main {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
  }
  header {
    justify-content: flex-start;
  }
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
